.all-latest {
  display: flex;
  flex-direction: column;
  align-items: center;

  .header {
    font-family: "Open Sans Light";
    font-size: 2.5em;
    font-weight: normal;
    text-transform: uppercase;
    text-align: center;
  }

  .cards-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 24px;
    row-gap: 72px;
    margin-top: 24px;
  }
}
